import React from 'react';
import Header from 'src/components/Header.jsx';
import { Helmet } from 'react-helmet';
import styles from 'src/styles/index.module.css';
import Core from 'src/Core/Core.js';
import Config from 'src/Config.js';

class Btn extends React.Component {
    constructor(props) {
        super(props);

        this.r = this.r.bind(this);
    };

    r() {
        if (this.props.t === 'b') {
            window.open(Core.path(this.props.href));
        } else {
            document.location.href = Core.path(this.props.href);
        };
    };

    render() {
        return (
            <button className={`${styles.btn} ${styles[this.props.id]}`} onClick={this.r}>
                {this.props.children}
            </button>
        );
    };
};

class Intro extends React.Component {
    render() {
        return (
            <div className={styles.Intro}>
                <div className={styles.iconWrapper}>
                    <img src={this.props.avatarURL} alt="" className={styles.icon} />
                </div>
                <h1 className={styles.name}>
                    {this.props.name}
                </h1>
                <p className={styles.smallDesc}>
                    Kanami - это няшный discord бот у которого есть Административные команды, Няшные реакции, Отображение информации, и другие команды. :3
                </p>
                <Btn id="supp" href="/r/support-server/" t="b">
                    Сервер поддержки
                </Btn>
                <Btn id="add" href="/r/add/" t="b">
                    Добавить бота
                </Btn>
                <Btn id="features" href="/features/" t="l">
                    Возможности бота
                </Btn>
            </div>
        );
    };
};

class Index extends React.Component {
    componentDidMount() {
        if (document.documentElement.clientWidth <= 425) {
            alert('ВНИМАНИЕ! Вы зашли на сайт с телефона или устройства с маленьким экраном. Версия сайта будет не полной, для полной версии включите режим компьютера в браузере или зайдите с ПК');
        };
    };

    render() {
        return (
            <div className={styles.root}>
                <Helmet>
                    <title>
                        {Config.SiteData.Title}Главная
                    </title>
                    <link rel="shortcut icon" href={Config.SiteData.IconURL} type="image/x-icon"/>
                    <meta name="viewport" content="width=device-width, initial-scale=1.0"/>
                    <meta name="google-site-verification" content="97ksVpUUrZL767gDpF5ouLuQfUrMgJ4P4wTAqc5JH_I" />
                </Helmet>
                <Header />
                <Intro avatarURL={Config.ProjectData.AvatarURL} name={Config.ProjectData.Name} />
            </div>
        );
    };
};

export default Index;